import React, { useEffect, useState } from 'react';
import { Row, Col, Button } from 'react-bootstrap';
import { useAlert } from 'react-alert';
import { motion } from "framer-motion";

import pdf from '../../assets/images/pdf.png';
import doc from '../../assets/images/doc.png';
import video from '../../assets/images/video.png';
import Modal from '../../components/uiElements/Modal';
import Loading from '../../components/uiElements/Loading';
import { animateConfigs, colors, helpers } from '../../constants';
import { _updateArtist, _getOneArtist } from '../../services/axios/api';
import { ChevronLeft, Instagram, Facebook, Globe, Wallet } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

export default function ViewArtist({ history, match: { params } }) {

    const [loading, setLoading] = useState(true);
    const [artist, setArtist] = useState(""); 
    const [showModal, setShowModal] = useState(false);
    const [deleteLoading, setDeleteLoading] = useState(false);
    
    const alert = useAlert();

    useEffect(() => {

        _getOneArtist(params.id)
        .then(({ data: { artist } }) => {
            console.log(artist, 'artist');
            setArtist(artist);
            setLoading(false);
        })
        .catch(err => {
            console.log(err);
            setLoading(false);
        })

        return () => {
            
        }
    }, [])

    function handleActivateDeactivate() {
        setDeleteLoading(true);
        const data = {
            status: artist.status === 0 ? 1 : 0
        }
        _updateArtist(params.id, data)
        .then(res => {
            console.log(res);
            setDeleteLoading(false);
            history.push('/');
            alert.success(`Artist ${artist.status === 1 ? 'activated' : 'deactivated'} successfully!`, {timeout:2000});
        })
        .catch(err => {
            console.log(err);
            setDeleteLoading(false);
            alert.error(`Failed to ${artist.status === 1 ? 'activate' : 'deactivate'}!`, {timeout:2000})
        })
    }

    const emptyState = () => {
        return <div style={{textAlign:'center', marginTop:'25%'}}>
            <div style={{fontSize:26, opacity:0.7}}>No NFTs added to this Artist</div>
            <Button onClick={() => history.push('/createnft')} className={globalClasses.outlinePrimaryBtn} style={{width:100, marginTop:10}}>
                New NFT
            </Button>
        </div>
    }

    const renderFiles = (cI) => {

        if(cI.fileType === "video") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='100px' width= '100px' style={{borderRadius:10, marginLeft:-10, cursor:'pointer'}} src={video} alt="upload"/>
            </div>
        } else if(cI.fileType === "image") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{objectFit:'contain', cursor:'pointer'}} src={cI.uri ? cI.uri : cI.fileUrl} alt="upload"/>
            </div>
        } else if(cI.fileType === "document") {
            <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={doc} alt="upload"/>
            </div>
        } else if(cI.fileType === "pdf") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={pdf} alt="upload"/>
            </div>
        } else {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <div>{cI.text ? cI.text : cI.date}</div> 
            </div>
        }

    }
    
    if(loading) {
        return <Loading/>;
    }
    
    return (
        <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
            <header className={globalClasses.flexRowSpace} style={{marginBottom: '40px'}}>
                <div onClick={() => history.goBack()} className={globalClasses.flexRow} style={{cursor:'pointer'}}>
                    <ChevronLeft size={16}/>
                    <span style={{fontWeight:600, fontSize:22, marginLeft:17, marginTop:-2}}>{artist.name}</span>
                </div>
                <div>
                    <Button onClick={() => setShowModal(true)} variant={artist.status === 0 ? "outline-danger" : "outline-primary"} className={artist.status === 0 ? globalClasses.outlineDangerBtn : globalClasses.outlinePrimaryBtn} style={{width:125}}>{artist.status === 0 ? 'Deactivate' : 'Activate'}</Button>
                    <Button onClick={() => history.push("/editartist", artist)} variant="outline-primary" className={globalClasses.outlinePrimaryBtn} style={{width:125, marginLeft:10}}>Edit Details</Button>
                </div>
            </header>
            <Row>
                <Col lg={5}>
                    <img style={{height:170, borderRadius:7}} src={artist.image} alt="profile"/>
                    {artist.description ? 
                    <>
                    <div className={classes.subHeadings}>Description</div>
                    <div style={{width:'85%'}}>
                        {artist.description} 
                    </div>
                    </> : null}
                    {artist.email || artist.phone ? 
                    <>
                    <div className={classes.subHeadings}>Contact details</div>
                    <div> {artist.phone ? `+91 ${artist.phone}` : ""} |  <span style={{color:colors.nakshBlue}}>{artist.email ? artist.email : ""} {}</span></div> 
                    </> : null}
                    {artist.state ? 
                    <>
                    <div className={classes.subHeadings}>Location</div>
                    <div>{artist.state}</div> 
                    </> : null}
                    {artist.organization ? 
                    <>
                    <div className={classes.subHeadings}>Organization</div>
                    <div>{artist.organization.name}</div> 
                    </> : null}
                    {artist.artform ? 
                    <>
                    <div className={classes.subHeadings}>Artform</div>
                    <div>{artist.artform.name}</div> 
                    </> : null}
                    {artist.custom ? 
                        artist.custom.map(cI => {
                            return renderFiles(cI);
                        }) : null
                    }
                    {artist.website || artist.facebook || artist.instagram ?
                    <>
                    <div className={classes.subHeadings}>Social Media</div>
                    <div className={globalClasses.flexRowSpace} style={{width:'30%', marginTop:5}}>
                        {artist.website ? <span onClick={() => helpers.openInNewTab(artist.website)}><Globe/></span> : null}
                        {artist.instagram ? <span onClick={() => helpers.openInNewTab(artist.instagram)}><Instagram/></span> : null}
                        {artist.facebook ? <span onClick={() => helpers.openInNewTab(artist.facebook)}><Facebook/></span> : null}
                    </div>
                    </> : null}
                </Col>
                <Col lg={7} style={{borderLeft:'1.5px solid rgba(0, 25, 76, 0.1)'}}>
                    <div style={{fontWeight:600, fontSize:20, marginBottom:10}}>Artists at {artist.name}.</div>
                    <Row>
                        {/* {renderArtwork()} */}
                        {emptyState()}
                    </Row>
                </Col>
            </Row>
            <Modal
                title={artist.status === 0 ? 'Deactivate artist' : 'Activate artist'} 
                show={showModal}
                body={artist.status === 0 ?
                    'Are you sure you want to deactivate this artist? The artists would be retained under the artist tab.' :
                    'Are you sure you want to activate this artist?'
                }
                valid={true}
                btnRight={deleteLoading ? "please wait..." : `${artist.status === 0 ? "Deactivate" : "Activate"}`}
                btnLeft="No, cancel"
                onSubmit={handleActivateDeactivate}
                onHide={() => setShowModal(false)}
            />
        </motion.div>
    )
}

