import React, { useState, useContext } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion";
import * as nearAPI from "near-api-js";

import { animateConfigs, colors, helpers } from '../../constants';
import pdf from '../../assets/images/pdf.png';
import doc from '../../assets/images/doc.png';
import video from '../../assets/images/video.png';
import { ChevronLeft } from '../../components/svgComponents';
import globalClasses from '../../constants/globalStyles.module.css';
import Badge from '../../components/uiElements/Badge';
import { WalletContext } from '../../App';
import Modal from '../../components/uiElements/Modal';
import Loading from '../../components/uiElements/Loading';
import classes from './artist.module.css';

const { utils, Account } = nearAPI;

export default function ViewNft({ history, match }) {

    const contextValue = useContext(WalletContext);
    
    const [loading, setLoading] = useState(true);
    const [nftPrice, setNftPrice] = useState("");
    const [nftDetails, setNftDetails] = useState("");
    const [modalShow, setModalShow] = useState(false);


    React.useEffect(() => {

        if(contextValue.wallet) {
            fetchNfts();
        }
        
    }, [contextValue.wallet]);

    const fetchNfts = () => {

        contextValue.wallet.account()
        .viewFunction(
            'nft1.abhishekvenunathan.testnet', 
            'nft_tokens', 
            {
                from_index: "0",
                limit: 1000 
            }
        )
        .then(res => {
            fetchListedNfts(res);
        })
        .catch(err => {
            setLoading(false);
            console.log(err);
        });

    }

    const fetchListedNfts = (allNfts) => {

        const paramsId = localStorage.getItem("paramsId");
        const primaryParamsId = localStorage.getItem("primaryParamsId");

        contextValue.wallet.account()
        .viewFunction(
            'market1.abhishekvenunathan.testnet', 
            'get_sales_by_nft_contract_id', 
            { 
                nft_contract_id: 'nft1.abhishekvenunathan.testnet',
                from_index: "0", 
                limit: 1000 
            }
        )
        .then(res => {
            
            const listedNfts = res;
            
            allNfts.map(nftItem => {
                const singleItem = listedNfts.find(t => t.token_id === nftItem.token_id);
                if(singleItem) {
                    nftItem["listed"] = true;
                    nftItem["price"] =  singleItem.sale_conditions;
                } else {
                    nftItem["listed"] = false;
                    nftItem["price"] = "NIL";
                }
            });

            const item = allNfts.find(item => item.token_id === (paramsId ? paramsId : primaryParamsId ? primaryParamsId : match.params.id));
            item.metadata['extra'] = JSON.parse(item.metadata.extra);
            setNftPrice(item.price !== "NIL" ? item.price : "");
            setNftDetails(item);
            if(paramsId) {
                return nearListing(item);
            }
        
            setLoading(false);
        })
        .catch(err => {
            setLoading(false);
            console.log(err);
        });
    }

    async function handleBurnNft() {

        const gas = 200000000000000;
        const attachedDeposit = 1;
        const FunctionCallOptions = {
            contractId: 'nft1.abhishekvenunathan.testnet',
            methodName: 'nft_transfer',
            args: {
                receiver_id: "0x0",
                approval_id: 0,
                token_id: nftDetails.token_id
            },
            gas,
            attachedDeposit
        };

        const data = await contextValue.wallet.account().functionCall(FunctionCallOptions);
    } 

    function listInMarketPlace() {
        nearStorage();
    }

    const nearStorage = async () => {
    
        const gas = 200000000000000;
        const attachedDeposit = utils.format.parseNearAmount("0.1");
        const FunctionCallOptions = {
            contractId: 'market1.abhishekvenunathan.testnet',
            methodName: 'storage_deposit',
            args: {},
            gas,
            attachedDeposit
        };
        
        localStorage.setItem("paramsId", match.params.id);
        localStorage.setItem("primaryParamsId", match.params.id);
        localStorage.setItem("nftPrice", nftPrice);
        const data = await contextValue.wallet.account().functionCall(FunctionCallOptions);

    }
    
    const nearListing = async (nftDetails) => {

        const msg = JSON.stringify({
            sale_conditions: localStorage.getItem("nftPrice")
        });
        const gas = 200000000000000;
        const attachedDeposit = utils.format.parseNearAmount("10");
        const FunctionCallOptions = {
            contractId: 'nft1.abhishekvenunathan.testnet',
            methodName: 'nft_approve',
            args: {
                account_id: 'market1.abhishekvenunathan.testnet',
                token_id: nftDetails.token_id,
                msg
            },
            gas,
            attachedDeposit
        };
        
        localStorage.removeItem("nftPrice");
        localStorage.removeItem("paramsId");
        const data = await contextValue.wallet.account().functionCall(FunctionCallOptions);
    }

    const updateNft = async () => {
    
        const gas = 200000000000000;
        const attachedDeposit = "1";
        const FunctionCallOptions = {
            contractId: 'market1.abhishekvenunathan.testnet',
            methodName: 'update_price',
            args: {
                nft_contract_id: 'nft1.abhishekvenunathan.testnet',
                token_id: nftDetails.token_id,
                price: nftPrice
            },
            gas,
            attachedDeposit
        };

        const data = await contextValue.wallet.account().functionCall(FunctionCallOptions);
    }

    const renderFiles = (cI) => {

        if(cI.fileType === "video") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='100px' width= '100px' style={{borderRadius:10, marginLeft:-10, cursor:'pointer'}} src={video} alt="upload"/>
            </div>
        } else if(cI.fileType === "image") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{objectFit:'contain', cursor:'pointer'}} src={cI.uri ? cI.uri : cI.fileUrl} alt="upload"/>
            </div>
        } else if(cI.fileType === "document") {
            <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={doc} alt="upload"/>
            </div>
        } else if(cI.fileType === "pdf") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={pdf} alt="upload"/>
            </div>
        } else {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <div>{cI.text ? cI.text : cI.date}</div> 
            </div>
        }

    }

    if(loading) {
        return <Loading/>;
    }

    return (
        <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
            <header className={globalClasses.flexRowSpace} style={{marginBottom: '40px'}}>
                <div onClick={() => history.push('/nfts')} className={globalClasses.flexRow} style={{cursor:'pointer'}}>
                    <ChevronLeft size={16}/>
                    <span style={{fontWeight:600, fontSize:22, marginLeft:17, marginTop:-2}}>{nftDetails.metadata.title}</span>
                </div>
                <div>
                    <Button variant="outline-dark" style={{width:125}}>Archive NFT</Button>
                    <Button 
                        onClick={() => setModalShow(true)} 
                        variant="primary" 
                        className={globalClasses.outlinePrimaryBtn} 
                        style={{marginLeft:10, height:42}}
                    >
                        {nftDetails.price === "NIL" ? "List in marketplace" : "Update nft price"}
                    </Button>
                </div>
            </header>
            <Row>
                <Col lg={5}>
                    <img style={{width:'100%', borderRadius:7}} src={nftDetails.metadata.media} alt="profile"/>
                </Col>
                <Col lg={7} style={{fontSize:18}}>
                    <Badge style={{width: nftDetails.listed ? 71 : 90, fontSize:16}}>
                        {nftDetails.listed ? "Listed" : "Unlisted"}
                    </Badge>
                    <div style={{fontWeight:600, fontSize:40, marginTop:5}}>{nftDetails.metadata.title}</div>
                    <div style={{fontWeight:600, fontSize:20, color: colors.nakshBlue}}>{nftDetails?.metadata?.extra?.artistName}</div>
                    <div style={{background:'#E6EEFF', borderRadius:7, padding:'10px 15px', marginTop:15, marginBottom:15}}>
                        <div style={{marginBottom:0}}>
                            <span style={{fontWeight:600, fontSize:28}}>{nftDetails.price !== "NIL" ? `${nftDetails.price} N` : nftDetails.price}</span> 
                            <span style={{fontSize:24, marginLeft:10}}>/ item</span>
                        </div>
                        {/* <div>Royalties: 0%</div> */}
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Quantity</div>
                        <div>1</div>
                    </div>
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Description</div>
                        <div>{nftDetails?.metadata?.description}</div>
                    </div>
                    {nftDetails?.metadata?.extra?.materialMediumUsed && 
                    <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Material</div>
                        <div>{nftDetails?.metadata?.extra?.materialMediumUsed}</div>
                    </div>}
                    {nftDetails?.metadata?.extra?.custom ? 
                        nftDetails.metadata.extra.custom.map(cI => {
                            return renderFiles(cI);
                        }) : null
                    }
                    {/* <div style={{marginBottom:15}}>
                        <div style={{fontWeight:600}}>Collection</div>
                        <div>Collection Name</div>
                    </div>
                    <div style={{marginBottom:25}}>
                        <div style={{fontWeight:600}}>Custom field</div>
                        <div>Aliqua id fugiat nostrud irure ex duis ea quis id quis ad et. Sunt qui esse pariatur duis deserunt mollit dolore cillum minim tempor enim. </div>
                    </div> */}
                    {/* <Button onClick={updateNft} variant="outline-danger" style={{width:'100%', border:'1px solid #E62F2F'}}>
                        Burn NFT
                    </Button> */}
                </Col>
            </Row>
            <Modal
                title={nftDetails.price === "NIL" ? 'List in Marketplace' : "Update nft price"} 
                show={modalShow}
                body={
                    <Col lg={12}>
                        <Form.Label className={globalClasses.label}>
                            Nft Price<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <Form.Control 
                            type="number" 
                            placeholder="Type here" 
                            onChange={(e) => setNftPrice(e.target.value)} 
                            value={nftPrice} 
                        />
                    </Col>
                }
                btnRight={nftDetails.price === "NIL" ? "Submit" : "Update"}
                onHide={() => setModalShow(false)}
                onSubmit={nftDetails.price === "NIL" ? listInMarketPlace : updateNft}
                valid={nftPrice ? true : false}
            />
        </motion.div>
    )
}

